export default {
  data: function () {
    return {
      new_name: '',
      first: false,
      second: false,
      third: false,
      edited_name: '',
      users: [
        {
          'name': 'Ismail',
          'avatar': 'https://avatars.dicebear.com/v2/male/:andwhattitov.svg',
          'edit_user': false
        }
      ],
      gender: false,
      check_gender: false
    }
  },
  directives: {
    focus: {
      // определение директивы
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods: {
    create () {
      var count = this.new_name.length
      var ava = 'https://avatars.dicebear.com/v2/' + this.gender + '/:' + '' + count + Math.random() + '' + '.svg'
      console.log(count, ava)
      this.users.push({
        name: this.new_name,
        avatar: ava,
        edit_user: false
      })
      this.new_name = ''
      this.gender = ''
    },
    edit (index) {
      this.users[index].edit_user = true
    },
    update (index) {
      if (!this.users[index].name) {
        this.third = 'э ээ, так нельзя братан!'
      } else {
        this.users[index].name = this.users[index].name
        this.users[index].edit_user = false
        this.third = false
      }
    },
    remove (index) {
      this.users.splice(index, 1)
    },
    validate: function () {
      if (!this.gender || !this.new_name) {
        this.first = 'камон, бро! выбери пол'
        this.second = 'ебани сюда имя либо кликуху'
      } else {
        this.create()
        this.first = false
        this.second = false
      }
    }
  }
}




